import React from "react";
import Terminal from './Terminal'

class Terminals extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            anchorEl: null,
            target: null,
            open: false,
            items: [],
        };
        this.handleClose = this.handleClose.bind(this)
        this.handleTerminalClick = this.onTerminalClick.bind(this)
    }
    
    handleClose(e) {
        if (!this.props.onClick) {
            return;
        }
        this.props.onClick(e);
    };

    onTerminalClick(e, data) {
        if (!this.props.onTerminalClick) {
            return;
        }
        this.props.onTerminalClick(e, data);
    };

    render() {
        const { error, isLoaded, items } = this.props;
        if (error) {
            return <div>Error: {error.message}</div>;
        } else if (!isLoaded) {
            return <div>Loading...</div>;
        } else if (items == null) {
            return null;
        } else {
            return (
                <svg height="100%" width="100%" viewBox="0 0 100 50">
                    {items.map(item => (
                        
                        <Terminal key={item.id} data={item} state={item.state} name={item.name} tenant={item.tenant} terminal_type={item.type} x={item.x} y={item.y} color={item.style} scale={0.10} id={item.id} rotate={item.angle} onClick={(e) => this.onTerminalClick(e, item)}></Terminal>
                       
                    ))}
                </svg>

            );
        }
    }
}

Terminals.defaultProps = {
    url: "http://localhost/api/terminals",
    items:[]
};

export default Terminals;