import React from "react";
import CssBaseline from '@material-ui/core/CssBaseline';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';

import TenantSelect from './component/TenantSelect'
import TenantContext from './component/TenantContext'
import Terminals from './component/Terminals'

import InfoLog from './component/InfoLog'
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';

import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';


import { withStyles } from '@material-ui/core/styles';
import { styles } from './styles/Style';
import { ThemeProvider, createMuiTheme } from '@material-ui/core/styles';
import './css/App.css';


const darkTheme = createMuiTheme({
  palette: {
    type: 'light',
  },
});

class App extends React.Component {
  constructor(props) {
    console.log("test",);
    super(props);
    this.state = {
      id: 0,
      open: false,
      log: [],
      open_menu: false,
      anchorEl: null,
      modalShow: false,
      selectedTerminalData: null,
      selectedTenant: null,
      tenant_error: null,
      tenant_isLoaded: false,
      tenant_items: [],
      tenant_url: "http://"+window.location.hostname+"/api/tenants",
      terminal_error: null,
      terminal_isLoaded: false,
      terminal_items: [],
      terminals_url: "http://"+window.location.hostname+"/api/terminals",
      update_terminal_url: "http://"+window.location.hostname+"/api/changetenant",
      login_url: "http://"+window.location.hostname+"/login/aci",
      logout_url: "http://"+window.location.hostname+"/logout"
    };
  }

  ws = new WebSocket("ws://"+window.location.hostname+":8080/v1/ws");

  componentDidMount() {
    //Lets load out data
    this.LoadTerminalData();
    this.LoadTenantData();

    this.ws.onopen = () => {
      // on connecting, do nothing but log it to the console
      this.ws.send(
        JSON.stringify({
          channel: "aci_control"
        }
        ));
      //console.log('connected')
    }
    this.ws.onmessage = evt => {
      // listen to data sent from the websocket server
      const message = JSON.parse(evt.data)
      const event = JSON.parse(message.event)


      if ("job_status_changed" in event) {
        this.addLogItem("job_status_changed", message.event)
        this.LoadTerminalData();
      } else if ("info" in event) {
        this.addLogItem("info", message.event)
      }
    }

    this.ws.onclose = () => {
      //console.log('disconnected')
      // automatically try to reconnect on connection loss
    }


  }


  handleOpenContextMenu = (event, data) => {
    //*******************************
    //Code to handle the context menu
    //*******************************
    this.setState({
      selectedTerminalData: data,
      open_menu: true,
      anchorEl: event.currentTarget,
    });
  };

  handleCloseContextMenu = (event, target, item) => {
    //***************************************
    //Code to handle closing the context menu
    //***************************************
    this.setState({
      selectedTenant: item,
      open_menu: false,
      anchorEl: null,
      modalShow: true,
    });
  };

  handleCancelContextMenu = (event) => {
    //***************************************
    //Code to handle closing the context menu
    //***************************************
    this.setState({
      open_menu: false,
      anchorEl: null,
    });
  };

  handleDrawerOpen = () => {
    this.setState({ open: true });
  };

  handleDrawerClose = () => {
    this.setState({ open: false });
  };

  addLogItem(status, message, type) {
    var log = this.state.log;
    var date = new Date().toLocaleString().replace(",", "").replace(/:.. /, " ");
    log.unshift({ date: date, status: status, message: message, type: type });
    this.setState({ log: log });
  }

  handleLogout = () => {
    //**********************
    //Load the terminal data
    //**********************
    var self = this;
    const axios = require('axios');
    axios.get(this.state.logout_url)
      .then(function (result) {
        window.location.href = self.state.login_url;
      })
      .catch(function (error) {
        window.location.href = self.state.login_url;
      })
  }

  handleUpdateTerminal = (event) => {
    //*******************
    //Update the terminal
    //*******************
    this.setState({
      modalShow: false,
    });
    this.ApiCallUpdateTerminal(this.state.selectedTerminalData, this.state.selectedTenant);

  };

  ApiCallUpdateTerminal(terminal, tenant) {
    //***************************************
    //Api to back end to update terminal data
    //***************************************
    const axios = require('axios');
    var self = this;
    self.addLogItem("updating tenant", "updating " + terminal.name + " to tenant " + tenant.name, "info")
    axios.post(this.state.update_terminal_url, {
      id: terminal.id,
      style: tenant.color,
      tenant: tenant.name
    })
      .then(function (response) {
        //self.handleLoadData();
        //console.log("response",response);
      })
      .catch(function (error) {
        self.addLogItem("updating tenant", tenant.name, "error")
      });
  }

  LoadTerminalData() {
    //**********************
    //Load the terminal data
    //**********************
    const axios = require('axios');
    var self = this;
    axios.get(this.state.terminals_url)
      .then(function (result) {
        self.setState({
          terminal_isLoaded: true,
          terminal_items: result.data
        });
      })
      .catch(function (error) {
        self.setState({
          terminal_isLoaded: true,
          terminal_error: error
        });
      })
  }

  LoadTenantData() {
    //**********************
    //Load the terminal data
    //**********************
    const axios = require('axios');
    var self = this;
    axios.get(this.state.tenant_url)
      .then(function (result) {
        self.setState({
          tenant_isLoaded: true,
          tenant_items: result.data
        });
      })
      .catch(function (error) {
        self.setState({
          tenant_isLoaded: true,
          tenant_error: error
        });
      })
  }

  DrawConfirmationDlg() {
    //*******************************
    //This is the confirmation dialog
    //*******************************
    var ip = (this.state.selectedTerminalData != null ? this.state.selectedTerminalData.name : "")
    var current_tenant = (this.state.selectedTerminalData != null ? this.state.selectedTerminalData.tenant : "")
    var new_tenant_name = (this.state.selectedTenant != null ? this.state.selectedTenant.name : "")
    return (
      <Dialog
        open={this.state.modalShow}
        onClose={() => { this.setState({ modalShow: false }) }}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">{"Update the Terminal " + ip + " to Tenant " + new_tenant_name + " ?"}</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            Please confirm that you have selected the correct terminal<br />
            Selected Terminal: <strong>{ip}</strong> <br />
            Current Tenant: <strong>{current_tenant}</strong>  <br />
            New Tenant: <strong>{new_tenant_name}</strong>
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => { this.setState({ modalShow: false }) }} color="primary">
            Cancel
          </Button>
          <Button onClick={() => { this.handleUpdateTerminal(); }} color="primary" autoFocus>
            Agree
          </Button>
        </DialogActions>
      </Dialog>
    );
  }


  render() {
    const { classes } = this.props;
    return (
      <ThemeProvider theme={darkTheme}>
        <div className={classes.container}>
          <CssBaseline />
          <AppBar position="static">
            <Toolbar>
              <div className={classes.toolbarButtons}>
                <Button onClick={this.handleLogout} color="inherit">Logout</Button>
              </div>
            </Toolbar>
          </AppBar>


          {this.DrawConfirmationDlg()}
          <main className={classes.content}>
            <div />
            <Container maxWidth="lg">
              <Grid container spacing={3}>
                {/* Chart */}
                <Grid item xs={10} md={10} lg={10}>
                  <Paper >
                    <TenantContext onCancel={this.handleCancelContextMenu} onClick={this.handleCloseContextMenu} anchorEl={this.state.anchorEl} open={this.state.open_menu} error={this.state.tenant_error} isLoaded={this.state.tenant_isLoaded} items={this.state.tenant_items}></TenantContext>
                    <Terminals error={this.state.terminal_error} isLoaded={this.state.terminal_isLoaded} items={this.state.terminal_items} onTerminalClick={this.handleOpenContextMenu}  ></Terminals>
                  </Paper>
                </Grid>
                {/* Recent Deposits */}
                <Grid item xs={1} md={1} lg={1}>
                  <Paper >
                    <TenantSelect error={this.state.tenant_error} isLoaded={this.state.tenant_isLoaded} items={this.state.tenant_items}></TenantSelect>
                  </Paper>
                </Grid>
              </Grid>
              <Grid container spacing={3}>
                {/* Chart */}
                <Grid item xs={10} md={10} lg={10}>
                  <Paper >
                    <InfoLog items={this.state.log} />
                  </Paper>
                </Grid>

              </Grid>
            </Container>
          </main>
        </div>
      </ThemeProvider>
    )

  }

}



App.defaultProps = {

};

export default withStyles(styles)(App);