import React from "react";
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
class InfoLog extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            items: [],
        };

    }


    render() {
        const { items } = this.props;
        return (
            <List dense={true} style={{ height: "300px", overflow: "auto" }}>


                {items.map((item, index) => {
                     if(item.type==="error"){
                        return (<ListItem key={index}> <ListItemText primary={<div>{item.date}: <strong style={{color: "red"}}>{item.status}</strong>={item.message}</div>} /> </ListItem>)
                    }else{
                        return (<ListItem key={index}> <ListItemText primary={<div>{item.date}: <strong style={{color: "blue"}}>{item.status}</strong>={item.message}</div>} /> </ListItem>)
                    }
                    
                })}
            </List>



        );

    }
}

InfoLog.defaultProps = {
    items: []
};

export default InfoLog;