import React from 'react';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import Fade from '@material-ui/core/Fade';
import Tooltip from "@material-ui/core/Tooltip";

export default class TenantContext extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            anchorEl: null,
            target: null,
            open: false,
            items: [],
        };
        //this.handleClick = this.handleClick.bind(this);
        //this.handleClose = this.handleClose.bind(this);
       
    }


    handleClick(event) {
        console.log("open");
    };

    handleCancel(e, c) {
        if (!this.props.onCancel) {
            this.setState({ open: false, anchorEl: null });
            return;
        }
       
        this.props.onCancel(e);
    };

    handleClose(e, c) {
        if (!this.props.onClick) {
            this.setState({ open: false, anchorEl: null });
            return;
        }
        console.log(c)
        this.props.onClick(e, this.state.anchorEl, c);
    };

    componentDidMount() {

    }


    static getDerivedStateFromProps(props, state) {
        return {
            open: props.open,
            anchorEl: props.anchorEl,
            items: props.items
        };
    }
    render() {
        const { error, isLoaded, items } = this.props;
        if (error) {
            return <div>Error: {error.message}</div>;
        } else if (!isLoaded) {
            return <div>Loading...</div>;
        } else if (items == null) {
            return null;
        } else {
            return (
                <Menu
                    id="fade-menu"
                    keepMounted
                    anchorEl={this.state.anchorEl}
                    anchorOrigin={{
                        vertical: "bottom",
                        horizontal: "right"
                    }}
                    getContentAnchorEl={null}
                    open={this.state.open}
                    TransitionComponent={Fade}
                >
                    {items.map(item => (
                        <Tooltip
                            key={item.name}
                            title={
                                <div>
                                    Name: {item.name} <br />
                                dn: {item.dn}
                                </div>
                            }
                            placement="top"
                        >
                            <MenuItem key={item.name} className={item.color} onClick={(evt) => this.handleClose(evt, item)}>{item.name}</MenuItem>
                        </Tooltip>
                    ))}
                    <MenuItem  onClick={(evt) => this.handleCancel(evt)} primary="secondary">Cancel</MenuItem>
                </Menu>

            );
        }
    }


}